import React from "react"
import { Link } from "gatsby"
import { GatsbyImage, getImage, StaticImage } from "gatsby-plugin-image"
import Layout from "../components/layout"
import Seo from "../components/seo"
import "../styles/main-article-column-template.css"

export default function Colmun({ pageContext }) {
  const { slug, title, updatedAt, authorName } = pageContext.column
  const image1 = getImage(pageContext.column.image1)
  const body1 = pageContext.column.body1.childMarkdownRemark.html
  const image2 = getImage(pageContext.column.image2)
  const body2 = pageContext.column.body2.childMarkdownRemark.html
  const image3 = getImage(pageContext.column.image3)
  const body3 = pageContext.column.body3.childMarkdownRemark.html
  const authorImage = getImage(pageContext.column.authorImage)
  const authorBiography = pageContext.column.authorBiography.childMarkdownRemark.html
  const twitterUrl = `https://twitter.com/share?url=https://www.hotsoul.jp/column/${slug}`
  const facebookUr = `http://www.facebook.com/share.php?u=https://www.hotsoul.jp/column/${slug}`
  const lineUrl = `https://social-plugins.line.me/lineit/share?url=https://www.hotsoul.jp/column/${slug}`

  return (
    <Layout>
      <Seo title={title} />
      <div className="wrap">
        <section className="mv_ac-cm">
          <div className="mv_ac-cm-inner">
            <p className="txt1"><span>コラム</span></p>
            <p className="txt2">小さな会社の経営者様にむけて<br/>ちょっと役に立つ、<br/>ちょっと面白い、<br/>そんなお話をご紹介します。</p>
          </div>
        </section>

        <section className="ac-cm">
          <h2>{title}</h2>
          <div className="ac-cm-cont">
            <div className="ac-cm-inner">
              <div className="item-box">
                <p className="postdate">{updatedAt}</p>
                <div className="item no-border first">
                  <div className="large-img">
                    <GatsbyImage image={image1} alt="イメージ1"/>
                  </div>
                  <div dangerouslySetInnerHTML={{ __html: body1 }} />
                </div>

                <div className="item no-border">
                  <div className="large-img">
                    <GatsbyImage image={image2} alt="イメージ2"/>
                  </div>
                  <div dangerouslySetInnerHTML={{ __html: body2 }} />
                </div>

                <div className="item no-border">
                  <div className="large-img">
                    <GatsbyImage image={image3} alt="イメージ3"/>
                  </div>
                  <div dangerouslySetInnerHTML={{ __html: body3 }} />
                </div>

                <div className="item">
                  <div className="flex-writer">
                    <div className="flex-writer-item left">
                      <p>この記事を書いた人</p>
                      <GatsbyImage image={authorImage} alt="この記事を書いた人"/>
                    </div>
                    <div className="flex-writer-item right">
                      <p className="name">{authorName}</p>
                      <div dangerouslySetInnerHTML={{ __html: authorBiography }} />
                    </div>
                  </div>
                </div>

                <div className="flex-share">
                  <ul className="sns">
                    <li>
                      <a href={twitterUrl} rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../images/icon-tw.png"
                          alt="twitterシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li> 
                    <li>
                      <a href={facebookUr} rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../images/icon-fb.png"
                          alt="facebookシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li>
                    <li>
                      <a href={lineUrl} rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../images/icon-line.png"
                          alt="LIMEシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li>
                  </ul>
			          </div>
                <br/>                
                <p className="archive"><Link to={`/column`}>他の記事も読む</Link></p>

              </div>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  )
}